.header{
	height: 60px;
	background: url('data:image/png;base64,');
	border-bottom: 6px solid theme-color("light_gray");
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	box-shadow: 0 1px 1px 0 rgba(0,0,0,0.4);
	flex:0 0 auto;
	z-index: 10;
	.switcher_view{
		a{
			background: #000;
			color: #fff;
			border-radius: 4px 0 0 0;
			+a{
				border-radius:  0 4px 0 0;
			}
			&.active{
				background: theme-color("light_gray");
				color: #000;
			}
		}
	}
}